body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card-wrapper {
  display: -webkit-flex;
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: center;
          justify-content: center;
}

.card {
  width: 200px;
  border-radius: 3px;
  overflow: hidden;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 20px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  box-shadow: 0px 0px 4px 2px #0000001f;
}

.card:hover {
  box-shadow: 0px 1px 6px 3px #0000001f;
}

.card__image {
  margin-bottom: 10px;
  max-height: 200px;
  overflow: hidden;
}

.card__image img {
  max-width: 100%;
}

.card__info {
  padding-bottom: 10px;
}

.card__info-row {
  padding: 5px 10px;
  font-size: 12px;
}

.filters {
  border-radius: 3px;
  max-width: 1200px;
  margin: 20px auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px;
  box-shadow: 0px 0px 4px 2px #0000001f;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.filters:hover {
  box-shadow: 0px 1px 6px 3px #0000001f;
}

.input-field {
  width: 200px;
  height: 30px;
  padding: 0 10px;
  font-size: 12px;
  border-radius: 3px;
  box-shadow: none;
  border: 1px solid lightgrey;
}

.input-group {
  display: -webkit-flex;
  display: flex;
  font-size: 12px;
  -webkit-flex-direction: column;
          flex-direction: column;
}

